const emojisIndex = Object.fromEntries(Object.entries(emojis).map(([key, value]) => [value, key]));

const isEmoji = text => {
  if (!text) {
    return false;
  }
  text = text.trim();
  if (text.length === 0) {
    return false;
  }
  const match = text.match(emojiRegex);
  if (!match) {
    return false;
  }
  return match[0].length == text.length;
}

const findEmojis = text => text.match(emojiRegex) || [];

// Export everything needed
export {
  emojis,
  emojisIndex,
  isEmoji,
  findEmojis,
  emojiRegex
};